.category {
  margin-bottom: 40px;

  @include onTablet {
    margin-bottom: 64px;
  }

  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.05);
      z-index: 10;
    }
  }

  &__link {
    position: relative;
    height: 0;
    padding-bottom: 100%;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;

    @include onTablet {
      font-size: 24px;
    }
  }

  &__button {
    @include pageGrid;

    &-element {
      grid-column: 1 / -1;

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }
}
